* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

@font-face {
  font-family: 'Poppins';
  font-style: normal;
  src: local(''),
    url('./poppins/poppins-v20-latin-regular.woff2') format('woff2'),

    url('./poppins/poppins-v20-latin-regular.woff') format('woff');
  
}

body {
  background: rgb(197, 182, 182);
  font-family: 'Poppins', sans-serif;
}

.container {
  display: flex;
  justify-content: start;
  width: 1440px;
  margin: 0 auto;
  min-height: 100vh;
  background: #f6f6fa;
}

.navBar {
  position: relative;
  width: 306px;
  padding: 36px 0 0 28px;
  background: #fff;
}

.navBar__logo {
  position: relative;
  height: 39px;
}

.logo__text {
  position: relative;
  bottom: 10px;
  font-weight: 500;
  font-size: 10px;
  line-height: 15px;
  letter-spacing: -0.01em;
  color: #838383;
}

.navBar__block {
  position: absolute;
  display: flex;
  justify-content: start;
  bottom: 15px;
  width: 155px;
  height: 42px;
}

.navBar__miniblock {
  margin-left: 12px;
}

.navBar__title {
  font-size: 14px;
  line-height: 21px;
  letter-spacing: 0.01em;
  font-weight: 600;
}

.navBar__subtitle {
  font-size: 12px;
  line-height: 18px;
  letter-spacing: 0.01em;
  color: #757575;
}



.menu {
  margin-top: 65px;
}

.menu__block {
  display: flex;
  justify-content: start;
  align-items: center;
  padding: 11px;
}

.menu__block p {
  margin-left: 15px;
  font-weight: 500;
  font-size: 14px;
  line-height: 21px;
  letter-spacing: -0.01em;
  color: #9197B3
}

.menu__link {
  display: block;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 10px;
  width: 250px;
  height: 46px;
  border-radius: 8px;
  padding: 11px 15px 11px 11px;
  text-decoration: none;
}

.menu__link-active {
  display: block;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 10px;
  width: 250px;
  height: 46px;
  border-radius: 8px;
  padding: 11px 15px 11px 11px;
  text-decoration: none;
  background-color: #5932EA;
}

.menu__link-active p {
  display: inline-block;
  margin-left: 15px;
  font-weight: 500;
  font-size: 14px;
  line-height: 21px;
  letter-spacing: -0.01em;
  color: white;
}

.menu__link-active img {
  color: white;
}

.menu__miniblock {
  display: flex;
  justify-content: start;
  align-items: center;
}

.menu__link p {

  display: inline-block;
  margin-left: 15px;
  font-weight: 500;
  font-size: 14px;
  line-height: 21px;
  letter-spacing: -0.01em;
  color: #9197B3;

}

.menu__arrow {
  width: 5px;
  height: 10px;
}

.content {
  padding: 40px 0 20px 70px;
}

.content__container {
  margin-top: 50px;
  width: 968px;
  box-shadow: 0px 10px 60px rgba(226, 236, 249, 0.9);
  border-radius: 30px;
}

.customers__titleblock {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 53px 40px 38px;
  padding-top: 30px;
}

.customers__title {
  font-size: 22px;
  line-height: 33px;
  letter-spacing: -0.01em;
}

.customers__subtitle {
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  letter-spacing: -0.01em;
  color: #16C098;
}

.customers__inputblock {
  position: relative;
}

.customers__search {
  padding-left: 40px;
  width: 216px;
  height: 38px;
  background: #F9FBFF;
  border-radius: 10px;
  border: none;
  font-size: 12px;
  line-height: 18px;
  letter-spacing: -0.01em;
  color: #B5B7C0;
}

.customers__inputblock img {
  position: absolute;
  left: 8px;
  top: 7px;
}

.customers__block {
  display: flex;
  justify-content: space-between;
  padding: 15px 38px;
}

.customers__text {
  font-size: 14px;
  line-height: 21px;
  letter-spacing: -0.01em;
}

.customers__text-grey {
  color: #B5B7C0;
}

.customers__text-black {
  color: black;
}

.listItem {
  position: relative;
  padding: 15px 38px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.col-1 {
  flex: 1;
}

.col-1-5 {
  flex: 1.5;
}

.col-2 {
  flex: 2;

}

.col-3 {
  flex: 3;
}

.line {
  width: 100%;
  min-height: 2px;
  background-color: #EEEEEE;
}

.listItem__line {
  position: absolute;
  width: 888px;
  bottom: 0px;
  min-height: 2px;
  background-color: #EEEEEE;
}

.customers__status {
  text-align: center;
  padding: 4px 0;
  line-height: 21px;
  letter-spacing: -0.01em;
  font-weight: 600;
  border-radius: 4px;
}

.customers__active {
  color: #008767;
  border: 2px solid #00B087;
  background: rgba(22, 192, 152, 0.38);
}

.customers__unactive {
  color: red;
  border: 2px solid red;
  background-color: rgba(229, 86, 86, 0.38);
}

.customers__block-2 {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
  padding: 15px 38px;
}

.customers__btnsblock {
  display: flex;
  justify-content: space-between;
  width: 200px;
}

.customers__btn {
  width: 26px;
  height: 24px;
  background: #F5F5F5;
  border: 1px solid #EEEEEE;
  border-radius: 4px;
  cursor: pointer;
}

.customers__btn-active {
  color: white;
  background: #5932EA;
  border: 1px solid #5932EA;
}
.generalText
{
  text-align: center;
  vertical-align: middle;
  padding: 20px 0;
  font-size: 25px;
  font-weight: 600;
}

@media (max-width: 1440px)
{
  .container
  {
    width: 1000px;
  }
  .navBar
  {
    width: 250px;
    padding: 36px 0 0 15px;
  }
  .navBar__logo img
  {
    width: 150px;
  }
  .menu__link
  {
    width: 180px;
  }
  .menu__link-active
  {
    width: 180px;
  }
  .content
  {
    padding-left: 20px;
  }
  .logo__text
  {
    bottom: 6px;
  }
  .content__container
  {
    width: 700px;
  }
  .customers__email
  {
    display: none;
  }
  .list__email
  {
    display: none;
  }
  .listItem__line
  {
    width: 620px;

  } 
  .generalText
  {
    font-size: 20px;
  }
}
@media (max-width: 1050px)
{
  .container
  {
    width: 100%;
    display: block;
  }
  .navBar 
  {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: start;  
    flex-wrap: wrap;
    padding: 20px 15px;
  }
  .menu
  {
    width: 100%;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    
    margin-top: 30px;
    padding: 20px;
    border: 3px solid blue;
    border-radius: 15px;
  }
  .navBar__block
  {
    top: 15px;
    right: 50px;
  }
  .menu__link-active
  {
    margin: 10px;
  }
  .menu__link
  {
    margin: 10px;
  }
  .content
  {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
  }
  .content h1
  {
    margin: 0 50px;
  }


}
@media (max-width: 700px)
{
  .navBar
  {
    justify-content: start;
  }
  .content__container
  {
    width: 90%;
  }
  .listItem__line
  {
    width: 90%;
  }
  .customers__company
  {
    display: none;
  }
}
@media (max-width: 630px)
{
  .customers__country
  {
    display: none;
  }
  .content__container
  {
    width: 98%;
  }
}
@media (max-width: 480px)
{
  .menu{
    padding: 5px;
  }
  .menu__block
  {
    width: 170px;
  }
  .menu__link-active
  {
    width: 170px;
    margin: 5px;
  }
  .menu__link
  {
    width: 170px;
    margin: 5px ;
  }
  .customers__phone
  {
    display: none;
  }
  .customers__search
  {
    width: 150px;
  }
  .menu__block
  {
    padding-top: 10px;
  }
  .customers__titleblock
  {
    flex-wrap: wrap;
  }
  .customers__inputblock
  {
    width: 300px;
    margin-top: 10px;
  }
  .customers__search
  {
    width: 100%;
  }
  .customers__block-2
  {
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 10px;
    padding-bottom: 30px;
  }
  .customers__btnsblock
  {
    margin-top: 10px;
  }
}
@media (max-width: 400px)
{
  .navBar__block
  {
    right: 20px;
  }
}

